import React, { useEffect, useRef, useState } from 'react';
import { motion } from 'framer-motion';
import './compani.css'
import comImg from './CompImg'

const Compani = () => {
    const [width, setWidth] = useState(0)
    const carousel= useRef()

    useEffect(()=>{
        setWidth(carousel.current.scrollWidth - carousel.current.offsetWidth)
    },[])

    return (
        <div className='compani'>
            <h2 className='text-color f-25 ttu tac'>companies our alumni are working at</h2>
            <motion.div ref={carousel} className='comCarousel'>
                <motion.div drag='x' dragConstraints={{right:0, left: -width}} className='comCarosule-inner'>
                    {comImg.map(img=>{
                        return(
                            <motion.div className='item'>
                                <img className='h100' src={img} alt="com Logo" />
                            </motion.div>
                        )
                    })}
                </motion.div>
            </motion.div>
        </div>
    );
};

export default Compani;