import React, { useEffect, useRef, useState } from 'react';
import { motion } from 'framer-motion';
import './event.css'
import CardSingle from './EventCardSingle/CardSingle';
const Event = () => {
    const eData = {
        date: "06",
        month: "jun",
        title: "best way to learn data science is to do data science",
        time: "11pm - 1pm",
        mentor: "@Lazaros Paschalidis - Mentor"
    }

    const [width, setWidth] = useState(0)
    const carousel= useRef()

    useEffect(()=>{
        setWidth(carousel.current.scrollWidth - carousel.current.offsetWidth)
    },[])
    return (
        <div className='event'>
            <div className="container">
                <div className="event-inner f-white">
                    <h2 className='f-45 ttc tac'>Upcoming Events</h2>
                    <motion.div ref={carousel} className='eventCrasol'>
                        <motion.div drag='x' dragConstraints={{ right: 0, left: -width }} className='eventCarosule-inner df'>
                            <CardSingle eData={eData}></CardSingle>
                        </motion.div>
                    </motion.div>
                </div>
            </div>
        </div>
    );
};

export default Event;